* {
    box-sizing: border-box;
}

body {
    background-color: #121212;
    color: white;
    font-family: monospace;
    display: flex;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    justify-content: center;
    align-items: center;
}


.container {
    width: 350px;
    text-align: center;
    padding: 30px 20px;
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.03);
}

.left {
    text-align: left;
}

h1 {
    font-size: 25px;
}

input {
    display: block;
    width: 100%;
    padding: 10px;
    font-size: 20px;
    border-radius: 20px;
    margin-bottom: 5px;
}

small {
    display: block;
    color: rgba(255, 255, 255, 0.7);
    margin-bottom: 10px;
    font-size: 16px;
}


.btn {
    background-color: #ffab14;
    color: black;
    padding: 10px;
    border-radius: 20px;
    font-size: 18px;
    cursor: pointer;
}

.btn.disabled {
    background-color: #ffca65;
}